"use client"
import React, { useEffect, useState } from 'react'
import PageHeading from '@/components/PageHeading'
import axios from 'axios'

const UsersPage = () => {
  const [users, setUsers] = useState<any[]>([])
  const [loading, setLoading] = useState(false)
  const [page, setPage] = useState(0)
  const [totalPages, setTotalPages] = useState(1)

  const fetchUsers = async (currentPage: number) => {
    setLoading(true)
    try {
      const res = await axios.get(`/user/all-users?page=${currentPage}&size=10&sortBy=userRegDate&sortDir=desc&type=ALL`)
      setUsers(res.data.result || [])
      setTotalPages(res.data.pagination?.totalPages || 1)
    } catch (error) {
      console.error('Failed to fetch users:', error)
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchUsers(page)
  }, [page])

  return (
    <div className='p-4'>
      <PageHeading title='All Users' />
      <div className='mt-4'>
        {loading ? (
          <div className='text-center'>Loading users...</div>
        ) : (
          <div className='grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4'>
            {users.map((user) => (
              <div
                key={user.userId}
                className='p-4 border border-gray-300 rounded-lg shadow-sm bg-white space-y-2'
              >
                <div className='font-semibold text-gray-800'>{user.userEmail}</div>
                <div className='text-sm text-gray-600'>Mobile: {user.mobileNo}</div>
                <div className='text-sm text-gray-600'>Registered: {user.userRegDate}</div>
                <div className='text-sm'>
                  Roles: {user.userRoles.map((ur: any) => ur.roles.roleTitle).join(', ')}
                </div>
                <div className='text-sm'>
                  Status:{' '}
                  <span className={user.enabled ? 'text-green-600' : 'text-red-600'}>
                    {user.enabled ? 'Enabled' : 'Disabled'}
                  </span>{' '}
                  |{' '}
                  <span className={user.deleted ? 'text-red-500' : 'text-green-500'}>
                    {user.deleted ? 'Deleted' : 'Active'}
                  </span>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Pagination */}
      <div className='flex justify-center mt-6 space-x-2'>
        <button
          onClick={() => setPage((p) => Math.max(0, p - 1))}
          disabled={page === 0}
          className='px-4 py-2 bg-gray-200 rounded disabled:opacity-50'
        >
          Previous
        </button>
        <span className='self-center text-sm'>
          Page {page + 1} of {totalPages}
        </span>
        <button
          onClick={() => setPage((p) => Math.min(totalPages - 1, p + 1))}
          disabled={page + 1 >= totalPages}
          className='px-4 py-2 bg-gray-200 rounded disabled:opacity-50'
        >
          Next
        </button>
      </div>
    </div>
  )
}

export default UsersPage
